
.cartcontrol{
  font-size: 0px;
}
.cart-decrease{
  line-height: 24px;
  font-size: 24px;
  display: inline-block;
  padding: 6px;
}
.cart-count{
  display: inline-block;
}
.cart-add{
  padding: 6px;
  color: rgb(0, 160, 220);
  line-height: 24px;
  font-size: 24px;
  display: inline-block;
}
.cart-count{
  vertical-align: top;
  width: 15px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: rgb(147, 153, 159)
}



